.searchBox{
    height: 100vh;
}

.searchBox_input{
    border: 2px solid rgb(60, 179, 235);
    min-height: 55px;
    border-radius: 50px;
    padding: 0px 10px;
}


.card{
    border-radius: 12px;
    overflow: hidden;
  }
  
  .card .pic{
    min-height: 270px;
    background-size: cover;
    background-position: top;
  
  } 

  .center{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  } 
  
.dark{
    position: fixed;
    width:100%;
    height: 100%;
    background-color: rgba(0,0,0,0.8);
    z-index: 999;
  }
  .dark_inside{
    width:100%;
    max-width: 500px;
    min-height: 180px;
    background-color: white;
    border:2px solid darkblue;
   
  }
  

